.clearfix:after {
  content: "";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

body {
  margin: 0 auto;
  -webkit-tap-highlight-color: transparent;

  max-width: 640px;
  // max-height: 640px;
  margin: 0 auto;
  // max-height: 100vh;
  position: relative;
}

@media all and (min-width: 980px) {
  body {
    max-width: 320px;
    max-height: 60vh;
    margin: 3vh auto 3vh;
    
  }
  .pac-container {
    border-radius: 3px;
    left: 3px!important;
    right: 3px!important;
    top: 60px!important;
  }
}

section {
  width: 100%;
  position: relative;
  margin: 0 auto;
  padding: 51px 0;
  height: 100%;
}
html,
body {
  height: 100%;
  min-height: 506px;
  overflow: hidden;
}

.container {
  padding: 0 6px;
  width: 100%;
  margin: 0 auto;
}

.search_field {
  position: absolute;
  top: 5px;
  left: 6px;
  right: 6px;
  z-index: 100;
  transform: translate3d(0, -200px, 0);
  opacity: 0;
  visibility: hidden;
  transition: all .3s linear;
  will-change: transform;

  &.show-search_field {
    transform: translate3d(0, 0, 0);
    opacity: 1;
    visibility: visible;
  }
}

#findCity_input {
  display: block;
  background-color: #fff;
  margin: 0 auto;
  font-size: 18px;
  padding: 0 11px 0 13px;
  text-overflow: ellipsis;
  width: 100%;
  height: 48px;
  background: #FFFFFF;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.15);
  filter: progid:DXImageTransform.Microsoft.dropshadow(OffX=0, OffY=2, Color='#000000');
}

.pac-logo:after {
  display: none;
}

.pac-container {
  border-radius: 3px;
}

.pac-item {
  line-height: 42px;
  border: none;
  font-size: 15px;
}

.pac-item-query {
  font-size: 15px;
}

.pac-icon {
  margin-top: 9px;
}

.view-content{
  height: 100%;
  position: relative;
    &.ng-animate {
        transition: all 0.5s ease-in-out;
        // position: absolute;
    }
    &.ng-enter,
    &.ng-leave {
        // left: 0;
        // right: 0;
        transition: all .5s ease-in-out;
    }
    &.ng-enter {
        opacity: 0;
    }
    &.ng-enter-active {
        opacity: 1;
    }
    &.ng-leave {
        opacity: 1;
    }
    &.ng-leave-active {
        opacity: 0;
    }
}

.view-wrap {
 height: 100%;
  position: relative;
}

