.kitchen-s1 {

  .view-content-kitchen {
    position: relative;
    height: 100%;
    // padding-bottom: 42px;
  }

  .container {
    position: relative;
    height: 100%;
  }

  .kitchen-page-result-wrapper {
        position: relative;
    height: 100%;
    &:after {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      display: block;
      content: '';
      height: 70px;
      background: -moz-linear-gradient(50% 92.62% 90deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
      background: -webkit-linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
      background: -webkit-gradient(linear, 50% 92.62%, 50% 3.93%, color-stop(0, rgba(255, 255, 255, 1)), color-stop(1, rgba(255, 255, 255, 0)));
      background: -o-linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
      background: -ms-linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
      -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFFFFF', endColorstr='#FFFFFF' ,GradientType=0)";
      background: linear-gradient(0deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFFFFF', endColorstr='#FFFFFF', GradientType=0);
      pointer-events: none;
    }
  }

  .kitchen-page-result {
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.15);
    filter: progid:DXImageTransform.Microsoft.dropshadow(OffX=0, OffY=2, Color='#000000');
    padding: 15px 10px 15px 18px;
    margin: 0;
    overflow: auto;
    border-radius: 3px;
    position: relative;
    height: 100%;

    &::-webkit-scrollbar {
      width: 2px !important;
    }

    /* Track */
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3) !important;
      -webkit-border-radius: 10px !important;
      border-radius: 10px !important;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
      -webkit-border-radius: 10px !important;
      border-radius: 10px !important;
      background: #41617D !important;
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5) !important;
    }
    &::-webkit-scrollbar-thumb:window-inactive {
      background: #41617D !important;
    }
  }

  .kitchen-category {
    position: relative;
    padding-left: 30px;
    margin-bottom: 7px;
  }

  .kitchen-category-val {
    position: absolute;
    left: 0;
    top: 0;
    @extend %sl;
    font-size: 15px;
    line-height: 36px;
    letter-spacing: 0.15px;
    color: #757A80;
    text-transform: uppercase;
  }

  .kitchen-category-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }


  .kitchen-category-item-name {
    @extend %sm;
    font-size: 15px;
    line-height: 36px;
    letter-spacing: 0.15px;
    color: #757A80;
  }


  .kitchen-category-item-count {
    @extend %sl;
    font-size: 15px;
    line-height: 36px;
    letter-spacing: 0.15px;
    color: #757A80;
  }



  .kitchen-page-btn {
    width: 100%;
    background-color: #54D6F0;
    border-radius: 3px;
    height: 36px;
    margin-top: 6px; // display: flex;
    // justify-content: center;
    // align-items: center;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      width: 15.4px;
      height: 15px;
      margin-right: 15px;
      fill: #ffffff;
    }

    span {
      @extend %sl;
      letter-spacing: 0.38px;
      font-size: 15px;
      color: #ffffff;
    }
  }
}