.kitchen-page-result-wrapper1 {
  height: 100%;
  position: relative;
  padding-bottom: 42px;
}

.kitchen-result-search {
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.15);
  filter: progid:DXImageTransform.Microsoft.dropshadow(OffX=0, OffY=2, Color='#000000');
  padding: 15px 10px 15px 10px;
  margin: 0;
  overflow: auto;
  border-radius: 3px;
  position: relative;
  height: 100%;

  &::-webkit-scrollbar {
    width: 2px !important;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3) !important;
    -webkit-border-radius: 10px !important;
    border-radius: 10px !important;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px !important;
    border-radius: 10px !important;
    background: #41617D !important;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5) !important;
  }
  &::-webkit-scrollbar-thumb:window-inactive {
    background: #41617D !important;
  }


  .kitchen-category {
    position: relative;
    padding-left: 30px;
    margin-bottom: 7px;
  }

  .kitchen-category-val {
    position: absolute;
    left: 0;
    top: 0;
    @extend %sl;
    font-size: 15px;
    line-height: 36px;
    letter-spacing: 0.15px;
    color: #757A80;
    text-transform: uppercase;
  }

  .kitchen-category-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }


  .kitchen-category-item-name {
    @extend %sm;
    font-size: 15px;
    line-height: 36px;
    letter-spacing: 0.15px;
    color: #757A80;
  }


  .kitchen-category-item-count {
    @extend %sl;
    font-size: 15px;
    line-height: 36px;
    letter-spacing: 0.15px;
    color: #757A80;
  }

  .k-r-btn-container {
    position: absolute;
    bottom: 0;
    left: 6px;
    right: 6px;
  }

  .kitchen-page-btn {
    width: 100%;
    background-color: #54D6F0;
    border-radius: 3px;
    height: 36px;
    margin-top: 6px; // display: flex;
    // justify-content: center;
    // align-items: center;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      width: 15.4px;
      height: 15px;
      margin-right: 15px;
      fill: #ffffff;
    }

    span {
      @extend %sl;
      letter-spacing: 0.38px;
      font-size: 15px;
      color: #ffffff;
    }
  }

  .kitchen-result-item-common {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  .search-page-result-item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 54px;

    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 1px;
      background-color: #EAECED;
    }
  }

  .search-result-i-logo {
    max-height: 30px;
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      max-width: 30px;
    }
  }

  .search-result-i-info {
    position: relative;
    width: 111px;
    overflow: hidden;
  }

  .search-result-i-rating {
    // position: absolute;
    // left: 0;
    // bottom: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;

    .star {
      margin-right: 2px;
    }
    .icon-svg-star {
      width: 9px;
      height: 9px;
      fill: #FFC300;
    }
  }

  .search-result-i-name {
    @extend %sm;
    font-size: 14px;
    line-height: 1.1;
    letter-spacing: 0.14px;
    color: #757A80;
  }

  .search-page-result-i-dist {
    height: 30px;
    width: 60px;
    @extend %sl;
    font-size: 13px;
    line-height: 1;
    letter-spacing: 0.13px;
    color: #757A80;
    display: flex;
    align-items: flex-end;
  }

  .search-result-i-dist__val {
    width: 40px;
    height: 30px;
    @extend %sl;
    font-size: 13px;
    line-height: 1;
    letter-spacing: 0.13px;
    color: #606060;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
  }
}